import * as React from "react";
import { PropsWithChildren } from "react";
import { useTranslation } from "react-i18next";

import CardList from "../CardList/CardList";
import DoNotMountComponent from "../DoNotMountComponent/DoNotMountComponent";
import { PropsCardList } from "../ResumeAndMedia/ResumeAndMedia";

type PropsSectorList = PropsWithChildren<{
  cardList?: PropsCardList;
}>;

const SectorList: React.FC<PropsSectorList> = ({ cardList }: PropsSectorList) => {
  if (!cardList || cardList.list.length < 1) return <DoNotMountComponent sms={"Falta cardList"} />;
  const { t } = useTranslation("global");
  return (
    <section className={`o-sector-list`}>
      <div className={`c-sector-list`}>
        <h2 className={"a-title-section"}>{t("Sectores")}</h2>
        {cardList && <CardList cardList={cardList.list} tagTitles={cardList.tagTitles} />}
      </div>
    </section>
  );
};

export default SectorList;
