import {graphql, PageProps} from "gatsby";
import React from "react";

import {Language} from "../../nubarchiva/settings/translations";
import {IPropsCard} from "../modules/Card/Card";
import Agreements from "../modules/content/Agreements/Agreements";
import FAQs from "../modules/content/FAQs/FAQs";
import {Faq} from "../modules/content/FAQs/IFaqs";
import ResumeAndDemo from "../modules/content/ResumeAndDemo/ResumeAndDemo";
import TitleResumeAndForm, {RenderForm} from "../modules/content/TitleResumeAndForm/TitleResumeAndForm";
import {CtaDemo, Hero} from "../modules/Hero/Hero";
import Layout from "../modules/Layout/Layout";
import ResumeAndMedia from "../modules/ResumeAndMedia/ResumeAndMedia";
import SectorList from "../modules/SectorList/SectorList";
import {SEO} from "../modules/SEO/SEO";
import TitleMarkAndResume from "../modules/TitleMarkAndResume/TitleMarkAndResume";
import {ILink} from "../modules/Layout/header/navbarLinks/NavbarLinks";
import {IPageProps} from "../types/IData";

const UsePage = ({ data }: IPageProps<Queries.UsePageQuery>) => {
  const cardSectorList: IPropsCard[] = [];
  data.use?.sectorList?.forEach((item) => {
    cardSectorList.push({
      title: item?.summary?.title,
      resume_html: item?.summary?.description,
    });
  });

  const listGoals: IPropsCard[] = data.use?.content?.goals as IPropsCard[];

  return (
    <Layout
      lang={data.use?.lang as Language}
      useLinks={data.allUse.nodes.map((item) => item.metas)}
      landingName="use"
      navbarLinksList={[...data.navbarLinks.nodes.map((item) => item.metas)]}
      legalLinks={data.allLegals?.nodes?.map((item) => item.childMarkdownRemark?.frontmatter) as ILink[] || []}
    >
      <Hero
        subtitle={data.use?.content?.hero?.subtitle}
        ctaDemo={CtaDemo.GO_TO_DEMO}
        modifier={data.use?.settings?.id}
        backgroundImage={data.backgroundHero?.sharp?.gatsbyImageData}
      >
        <h1 className={"hero__title"}>{data.use?.content?.hero?.title}</h1>
      </Hero>
      <TitleMarkAndResume
        nameSection={"main"}
        titleMarkAndResume={{ ...data.use?.content?.main, tagTitle: "h2" }}
        cardList={{ list: listGoals, tagTitles: "h3" }}
      />
      <ResumeAndMedia
        resumeAndMedia={{ tagTitle: "h2", ...data.use?.content?.resumeAndMedia }}
        nameSection={"history-file"}
        cardList={{ list: data.use?.content?.resumeAndMedia?.cards as IPropsCard[], tagTitles: "h3" }}
        image={data.resumeAndMediaPhoto?.sharp?.gatsbyImageData}
      />
      <Agreements agreements={{ tagTitle: "p" }} />
      <ResumeAndDemo image={data.resumeAndDemoPhoto?.sharp?.gatsbyImageData} data={{ ...data.use?.content?.resumeAndDemo, tagTitle: "p" }} />
      <SectorList cardList={{ list: cardSectorList, tagTitles: "h3" }} />
      <FAQs
        title={data.use?.content?.faqs?.title}
        faqsLists={data.use?.content?.faqs?.FaqsList as Faq[]}
        tagTitle={"h2"}
      />
      <TitleResumeAndForm
        data={{ ...data.use?.content?.form, tagTitle: "p" }}
        nameSection={"subscribe"}
        renderForm={RenderForm.SUBSCRIBE}
      />
    </Layout>
  );
};

export default UsePage;

export const Head = ({ data }: IPageProps<Queries.UsePageQuery>) => (
  <SEO
    title={data.use?.metas.title}
    description={data.use?.metas.description}
    thumbnailWithExtension={data.use?.metas.thumbnail}
    lang={data.use?.lang as Language}
    slug={data.use?.metas.slug}
    canonical={data.use?.metas.slug}
    hrefLangList={data.hrefLangsList?.nodes?.map((node) => ({
      hrefLang: node.lang,
      slug: node.metas.slug,
    }))}
  />
);

export const query = graphql`
  query UsePage($id: String!, $settingsId: String!, $lang: String = "es_es") {
    use(id: { eq: $id }) {
      ...seo
      lang
      settings {
        id
      }
      content {
        hero {
          title
          subtitle
        }
        main {
          title
          mark_html
          resume_html
        }
        goals {
          title
          resume_html
        }
        resumeAndMedia {
          title
          resume_html
          cards {
            title
            resume_html
          }
        }
        resumeAndDemo {
          title
          resume_html
        }
        faqs {
          title
          FaqsList {
            answer
            question
          }
        }
        form {
          title
        }
      }
      sectorList {
        summary {
          title
          description
        }
      }
    }
    backgroundHero: file(name: { eq: $settingsId }, relativeDirectory: {eq: "hero"}) {
      sharp: childImageSharp {
        gatsbyImageData
      }
      ext
    }
    resumeAndMediaPhoto: file(name: { eq: $settingsId }, relativeDirectory: {eq: "resume-and-media"}) {
      sharp: childImageSharp {
        gatsbyImageData
      }
      ext
    }
    resumeAndDemoPhoto: file(name: { eq: $settingsId }, relativeDirectory: {eq: "resume-and-demo"}) {
      sharp: childImageSharp {
        gatsbyImageData
      }
      ext
    }
    allUse(filter: { lang: { eq: $lang } }, sort: { settings: { order: ASC } }) {
      nodes {
        metas {
          slug
          name
        }
        summary {
          title
          description
        }
      }
    }
    navbarLinks: allPage(filter: { lang: { eq: $lang } }, sort: { settings: { order: ASC } }) {
      nodes {
        metas {
          name
          slug
        }
      }
    }
    hrefLangsList: allUse(filter: { settings: { id: { eq: $settingsId } } }) {
      nodes {
        metas {
          slug
        }
        lang
      }
    }
    allLegals: allFile(
      filter: { sourceInstanceName: { eq: "legal" }, relativeDirectory: { eq: $lang } }
      sort: { childMarkdownRemark: { frontmatter: { order: ASC } } }
    ) {
      nodes {
        childMarkdownRemark {
          frontmatter {
            slug
            name: title
          }
          id
        }
        relativeDirectory
      }
    }
  }
`;
