import {GatsbyImage, StaticImage} from "gatsby-plugin-image";
import * as React from "react";
import { PropsWithChildren } from "react";
import ReactHtmlParse from "react-html-parser";

import { IData } from "../../../types/IData";
import { logoCircle_svg } from "../../Company/logos_element";
import DoNotMountComponent from "../../DoNotMountComponent/DoNotMountComponent";
import {useTranslation} from "react-i18next";
// import { useModal } from "../../Modals/useModal";
// import { withModal } from "../../Modals/withModal";
// import PopupVideo from "./PopupVideo/PopupVideo";

type PropsResumeAndDemo = PropsWithChildren<{
  data: IData;
  image?: any;
  nameSection?: string | null | undefined;
}>;

const ResumeAndDemo: React.FC<PropsResumeAndDemo> = ({ data, nameSection, image }: PropsResumeAndDemo) => {
  if (!data?.title) return <DoNotMountComponent sms={"Falta titulo en any example"} />;
  if (!data?.resume_html) return <DoNotMountComponent sms={"Falta resume en any example"} />;
  const { t } = useTranslation("global");

  const title_render = React.createElement(data?.tagTitle || "p", { className: "a-title-section" }, data.title);
  // const [has_modal, toggleModal] = useModal();
  // const VideoWithModal = withModal(
  //   PopupVideo,
  //   {},
  //   {
  //     has_modal,
  //     toggleModal,
  //   }
  // );
  // function click() {
  //   toggleModal();
  // }
  return (
    <section className={`s-${nameSection} o-resume-demo`}>
      <div className={`c-resume-demo`}>
        <div className={"resume-demo__resume"}>
          <div>{title_render}</div>
          <div className={"resume-demo`__text"}>{ReactHtmlParse(data.resume_html)}</div>
          {/*<Trans t={t} components={{ b: <b /> }}>*/}
          {/*  {t("ResumeAndDemo.text_html")}*/}
          {/*</Trans>*/}
        </div>
        <div className={`resume-demo__video is-text-center`}>
          {/*<div className="any-examples__play">{play_svg}</div>*/}
          <div className="resume-demo__div">
            {image ? <GatsbyImage
                    className={"resume-demo__img"}
                    alt={`${t("Image")} ${data.title}`}
                    image={image}
                    objectFit={"cover"}
                    objectPosition={"center"}
                /> :
            <StaticImage
              imgClassName="resume-demo__img"
              src="./thumbnail-video.jpg"
              alt={`${t("Image")} ${data.title}`}
            />}
          </div>
          {/*<Link className={"a-button a-button--without-border a-button--full"} to={`/demo/`}>*/}
          {/*  {data.button}*/}
          {/*</Link>*/}
          {logoCircle_svg}
          {/*<VideoWithModal />*/}
        </div>
      </div>
    </section>
  );
};

export default ResumeAndDemo;
