import { Link } from "gatsby";
import {GatsbyImage, StaticImage} from "gatsby-plugin-image";
import * as React from "react";
import ReactHtmlParse from "react-html-parser";

import { IPropsCard } from "../Card/Card";
import CardList from "../CardList/CardList";
import DoNotMountComponent from "../DoNotMountComponent/DoNotMountComponent";
import { ILink } from "../Layout/header/navbarLinks/NavbarLinks";
import {useTranslation} from "react-i18next";

export type IResumeAndMedia = {
  title?: string | null | undefined;
  tagTitle?: "h1" | "h2" | "h3" | "p" | "h4" | "h5" | "h6";
  resume_html?: string | null | undefined;
  button?: ILink | null | undefined;
};

export type PropsCardList = {
  list: IPropsCard[];
  tagTitles?: "h2" | "h3" | "p" | "h4" | "h5" | "h6";
};

type PropsResumeAndMedia = {
  resumeAndMedia: IResumeAndMedia;
  cardList?: PropsCardList;
  nameSection: string;
  image?: any;
};

const ResumeAndMedia: React.FC<PropsResumeAndMedia> = ({
  resumeAndMedia,
  cardList,
  nameSection,
                                                         image,
}: PropsResumeAndMedia) => {
  if (!resumeAndMedia) return <DoNotMountComponent sms={"Falta resumeAndMedia"} />;
  if (!nameSection) return <DoNotMountComponent sms={"Falta nameSection"} />;
  if (!resumeAndMedia.title) return <DoNotMountComponent sms={"Falta title"} />;
  if (!resumeAndMedia.resume_html) return <DoNotMountComponent sms={"Falta resume_html"} />;
  const title_render = React.createElement(
    resumeAndMedia?.tagTitle || "p",
    { className: "a-title-section" },
    ReactHtmlParse(resumeAndMedia.title)
  );
  const { t, i18n } = useTranslation("global");

  return (
    <section className={`s-${nameSection} o-resume-media`}>
      <div className={`c-resume-media`}>
        <div className="resume-media__columns">
          <div className={"resume-media__columns-left"}>
            {title_render}
            <div className={"resume-media__text"}>{ReactHtmlParse(resumeAndMedia.resume_html)}</div>
            {resumeAndMedia.button?.name && (
              <Link
                className={"a-button a-button--without-border a-button--blue-light"}
                to={`/${resumeAndMedia.button?.slug}/`}
              >
                {resumeAndMedia.button?.name}
              </Link>
            )}
          </div>
          <div className={"resume-media__columns-right"}>
            {image ?    <GatsbyImage
                className={"resume-media__img"}
                alt={`${t("Image")} ${resumeAndMedia.title}`}
                image={image}
                objectFit={"cover"}
                objectPosition={"center"}
              /> :
            <StaticImage
              imgClassName="resume-media__img"
              src="./img-device.png"
              alt={`${t("Image")} ${resumeAndMedia.title}`}
              width={500}
              height={530}
            />
            }
          </div>
        </div>
        {cardList && <CardList cardList={cardList.list} tagTitles={cardList.tagTitles} />}
      </div>
    </section>
  );
};

export default ResumeAndMedia;
